<template>
    <div class="self-leaflet-base-layer" :style="customStyle" :class="customClass"
        v-if="this.showChangeBtn && showChangeBtnArr.length>1">
        <template v-if="showStyle=='topright'">
            <div class="base-layer-btn" v-for="(baseType,index) of showChangeBtnArr" :key="'baseType-'+index"
                @click="handleChangeBase(baseType)">
                <slot :name="baseType" :scope="baseType">
                    <div class="btn-image"
                        :class="['btn-image-'+baseType, currentBaseType==baseType?'current-active':'']">
                        <div class="btn-txt">{{titleServeLayerType[baseType]}}</div>
                    </div>
                </slot>
            </div>
        </template>
        <template v-else-if="showStyle=='bottomright'">
            <div class="base-layer-style-2" @click.stop @dblclick.stop @mousemove.stop>
                <div class="base-layer-style-2-content">
                    <div class="base-layer-btn" v-for="(baseType,index) of showChangeBtnArr" :key="'baseType-'+index"
                        @click="handleChangeBase(baseType)" :style="{'order':currentBaseType==baseType?1:2}">
                        <slot :name="baseType" :scope="baseType">
                            <div class="btn-image"
                                :class="['btn-image-'+baseType, currentBaseType==baseType?'current-active':'']">
                            </div>
                            <div class="btn-txt">{{titleServeLayerType[baseType]}}</div>
                        </slot>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
    import baseLayerMixin from '../mixins/baseLayerMixin.js'
    export default {
        mixins: [baseLayerMixin],
        name: 'WlBaseLayer',
        props: {
            customStyle: {
                default () {
                    return {
                        position: 'absolute',
                        right: '10px',
                        top: '10px'
                    }
                }
            },
            customClass: {
                default: '',
            },
            // 切换按钮展示的位置，不同的风格，此处以后待优化
            // 有效值为  topright  bottomright
            showStyle: {
                default: 'topright',
            },
            // 是否显示切换地图的按钮，为false，即是显示一种类型的底图
            showChangeBtn: {
                default: true,
            },
            /**
             * 需要显示的底图类型，默认显示矢量图层
             * 用数组表示  ['skymapVec', 'skymapTopography', 'skymapImage']
             * skymapVec   矢量
             * skymapTopography  地形
             * skymapImage  影像
             */
            defaultTileServe: {
                type: Array,
                default () {
                    return ['skymapVec']
                }
            },
            // 自定义服务类型，可传值：wmts
            serviceType: {
                type: String,
                default: ''
            },
            // 自定义服务的配置，目前仅支持wmts后续扩展
            serviceConfig: {
                type: Object,
                default () {
                    return {}
                }
            }
        },
        data() {
            return {
                titleServeLayerType: {
                    skymapVec: '矢量',
                    skymapTopography: '地形',
                    skymapImage: '影像',
                },
                currentBaseType: '',
            }
        },
        computed: {
            //确保传进来的底图类型存在我们提供的底图服务中
            showChangeBtnArr() {
                return this.defaultTileServe.filter(item => this.titleServeLayerType[item]);
            }
        },
        methods: {
            // 切换底图的模式
            handleChangeBase(baseType) {
                this.changeTileServe(baseType);
            },
            changeTileServe(baseType) {
                this.currentBaseType = baseType;
                this.changeBaseType(baseType);
            },
            // 得到地图的图层
            getBaseLayer() {
                return this.baseLayer;
            },
            setcustomServe(type, config) {
                if (type === 'wmts') {
                    this.setWmtsService(config);
                }
            }
        },
        mounted() {
            if (this.serviceType) {
                this.setWmtsService(this.serviceConfig);
            } else if (this.showChangeBtnArr.length > 0) {
                let type = this.showChangeBtnArr[0]
                this.currentBaseType = type;
                this.changeBaseType(type);
                // L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', { foo: 'bar' }).addTo(this.parentMap);
            } else {
                this.$alert('请传入有效的底图类型', '提示');
            }
        },
    }
</script>

<style lang="less">
    .self-leaflet-base-layer {
        position: relative;
        z-index: 999;
        width: fit-content;
        background-color: #ffffff;
        padding: 4px 2px;
        box-shadow: 0 0 6px #ccc;

        .base-layer-btn {
            display: inline-block;
            margin: 0 6px;
        }

        .btn-image {
            width: 68px;
            height: 70px;
            background-image: url('../image/icon.png');
            background-repeat: no-repeat;
            background-position: -240px -50px;
            position: relative;

            .btn-txt {
                color: #ffffff;
                line-height: 24px;
                background-color: rgba(0, 0, 0, 0.5);
                position: absolute;
                width: 100%;
                left: 0;
                bottom: 0;
                text-align: center;
            }
        }

        .current-active {
            .btn-txt {
                // color: #f0f403;
                font-weight: bold;
                background-color: rgba(0, 0, 0, 0.7);
            }
        }

        .btn-image-skymapVec {
            background-position: -240px -48px;
        }

        .btn-image-skymapTopography {
            background-position: -83px -48px;
        }

        .btn-image-skymapImage {
            background-position: -162px -48px;
        }


        .base-layer-style-2 {
            width: 81px;
            overflow: hidden;
            transition: all 1s;
            background: #fff;
            box-shadow: 0 0 3px #ccc;

            &:hover {
                cursor: pointer;
                width: fit-content;
            }

            .base-layer-style-2-content {
                display: inline-flex;
            }

            .base-layer-btn {
                position: relative;

                .btn-txt {
                    background-color: #3E7EF9;
                    position: absolute;
                    bottom: 0px;
                    right: 0;
                    color: #fff;
                    padding: 2px 8px;
                }
            }
        }
    }
</style>