<template>
    <div :id="id" class="leaflet-map-container" :style="defaultStyle" :class="defaultClass">
        <slot v-if="ready"></slot>
    </div>
</template>

<script>
    import L from "leaflet";
    import initMapMixin from '../mixins/initMapMixin.js';
    import { mousePosition } from '../map/mousePosition.js';

    export default {
        inheritAttrs: false,
        mixins: [initMapMixin],
        props: {
            id: {
                type: String,
                default: "map",
                require: true,
            },
            mapOptions: {
                type: Object,
                detault: () => ({})
            },
            defaultStyle: {
                default () {
                    return {
                        height: '100%',
                        'min-height': '100px',
                        border: '1 px solid# ccc',
                    }
                }
            },
            defaultClass: {
                default () {
                    return ''
                }
            },
            // 在地图上显示鼠标的位置
            mousePosition: {
                type: Boolean,
                default: false,
            },
            // 是否显示比例尺
            showScale: {
                type: Boolean,
                default: false,
            },
        },
        name: 'WangLeaflet',
        data() {
            return {
                ready: false
            };
        },
        methods: {
            // 使用过程中，
            getMap() {
                return this.mymap;
            },
            getBaseLayer() {
                return this.baseLayer;
            },
            getEditLayer() {
                return this.editLayer;
            },
            getAllLayer() {
                return {
                    map: this.mymap,
                    baseLayer: this.baseLayer,
                    editLayer: this.editLayer,
                }
            },
            // 初始化比例尺
            initScale(obj) {
                let options = { maxWidth: 150, metric: true, imperial: false };
                if (obj) {
                    options = Object.assign({}, options, obj);
                }
                L.control.scale(options).addTo(this.mymap);
            }
        },
        mounted() {
            this.ready = true;
            this.$emit('ready', this.mymap);

            this.mousePosition && this.mymap.addControl(mousePosition());

            this.showScale && this.initScale();
        }
    }
</script>
<style lang="less">
    .leaflet-map-container {
        .my-measure-icon {
            border: none;

            .tips {
                display: inline-block;
                padding: 6px 12px;
                border: 1px solid #cccccc;
                box-shadow: 0 0 3px #cccccc;
                background-color: #ffffff;
                white-space: nowrap;
                font-size: 14px;
                line-height: 1.5;
            }

            .tips-top {
                position: relative;
                top: -38px;
            }
        }
    }
</style>