import WangLeaflet from './src/leafletMap.js';
import WlBaseLayer from './src/baseLayer.js';
import WlEditLayer from './src/editLayer.js';

const components = [
    WangLeaflet,
    WlBaseLayer,
    WlEditLayer
];

const install = function(Vue, opts = {}) {
    components.forEach(component => {
        Vue.component(component.name, component);
    });
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    WangLeaflet,
    WlBaseLayer,
    WlEditLayer
}